import { Fragment, useState } from "react";
import { Dialog, Transition } from "@headlessui/react";

interface ContractActionModalProps {
  isOpen: boolean;
  onClose: () => void;
  contractId: number;
  title: string;
  type: "extend" | "terminate";
  confirmLabel: string;
  confirmColor?: "green" | "red";
  dateLabel: string;
  onSubmit: (date: string, reason: string) => void;
}

export function ContractActionModal({
  isOpen,
  onClose,
  contractId,
  title,
  confirmLabel,
  type,
  confirmColor = "green",
  dateLabel,
  onSubmit,
}: ContractActionModalProps) {
  const [date, setDate] = useState("");
  const [reason, setReason] = useState("");

  const confirmClass =
    confirmColor === "green"
      ? "bg-green-600 hover:bg-green-700"
      : "bg-red-600 hover:bg-red-700";

  return (
    <Transition.Root show={isOpen} as={Fragment}>
      <Dialog
        as="div"
        className="fixed inset-0 z-10 overflow-y-auto"
        onClose={onClose}
      >
        <div className="flex items-center justify-center min-h-screen px-4">
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0"
            enterTo="opacity-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <div className="fixed inset-0 bg-black bg-opacity-30" />
          </Transition.Child>

          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0 translate-y-4"
            enterTo="opacity-100 translate-y-0"
            leave="ease-in duration-200"
            leaveFrom="opacity-100 translate-y-0"
            leaveTo="opacity-0 translate-y-4"
          >
            <div className="bg-white rounded-lg overflow-hidden shadow-xl transform transition-all max-w-md w-full p-6">
              <Dialog.Title className="text-lg font-medium text-gray-900 mb-4">
                {title} #{contractId}
              </Dialog.Title>

              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    {dateLabel}
                  </label>
                  <input
                    type="date"
                    className="mt-1 w-full border border-gray-300 rounded-md px-3 py-2"
                    value={date}
                    onChange={(e) => setDate(e.target.value)}
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    Lý do
                  </label>
                  <textarea
                    className="mt-1 w-full border border-gray-300 rounded-md px-3 py-2"
                    rows={3}
                    value={reason}
                    onChange={(e) => setReason(e.target.value)}
                  />
                </div>
              </div>

              <div className="mt-6 flex justify-end space-x-2">
                <button
                  onClick={onClose}
                  className="px-4 py-2 bg-gray-200 rounded hover:bg-gray-300"
                >
                  Hủy
                </button>
                <button
                  onClick={() => onSubmit(date, reason)}
                  className={`px-4 py-2 text-white rounded disabled:opacity-50 ${confirmClass}`}
                  disabled={!date || !reason}
                >
                  {confirmLabel}
                </button>
              </div>
            </div>
          </Transition.Child>
        </div>
      </Dialog>
    </Transition.Root>
  );
}
